/* 🔳 Фоновый цвет	Тёмно-графитовый	                              #121212
🟩 Основной акцент	Ядовито-зелёный (под Dota-style highlight)	    #00FF94
🔘 Вторичный акцент	Синий с неоновым эффектом	                      #00BFFF
⚪️ Белый текст	Ярко-белый для контраста	                         #F1F1F1
⚫️ Тёмный текст	Серо-синий (вторичный текст)	                     #B0BEC5
🔲 Карточки/блоки	Графит с лёгким теневым отливом	                 #1E1E1E
🟥 Ошибка/предупрежд.	Ярко-красный/оранжевый	                     #FF4C4C
🟨 Уведомления	Янтарный для всплывающих подсказок	               #FFC107 */

body {
  background-color: #121212;
  padding-top: 70px;
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  color: #F1F1F1;
  position: relative;
  overflow-x: hidden;
}

/* Фоновой узор */
body::before {
  content: "";
  position: fixed;
  inset: 0;
  background-image:
    radial-gradient(circle at 15% 20%, rgba(0,255,148,0.07) 0px, transparent 250px),
    radial-gradient(circle at 85% 75%, rgba(0,191,255,0.07) 0px, transparent 250px),
    /* шестиугольный паттерн */
    repeating-linear-gradient(60deg, rgba(255,255,255,0.015) 0px, rgba(255,255,255,0.015) 1px, transparent 1px, transparent 35px),
    repeating-linear-gradient(-60deg, rgba(255,255,255,0.015) 0px, rgba(255,255,255,0.015) 1px, transparent 1px, transparent 35px);
  background-size: cover;
  background-attachment: fixed;
  animation: hexagonFlow 80s linear infinite;
  z-index: -1;
}

/* Медленное движение узора */
@keyframes hexagonFlow {
  0%   { background-position: 0 0, 0 0, 0 0, 0 0; }
  50%  { background-position: 200px 200px, -200px -200px, 300px 100px, -300px -100px; }
  100% { background-position: 0 0, 0 0, 0 0, 0 0; }
}

h1 {
  color: #00FF94;
  font-weight: 700;
}

h1, h2 {
  font-weight: 700;
}

button {
  background-color: #00BFFF;
  border: none;
  border-radius: 6px;
  height: 40px;
  width: 140px;
  font-weight: 600;
}

button:hover {
  cursor: pointer;
  background-color: #5bd6ff;
}

.resultInput {
  width: 600px;
  margin-top: 8px;
  height: 20px;
  max-height: 200px;
  word-break: normal;
  border: none;
  border-radius: 10px;
  padding-left: 6px;
}

.banner h1 {
  cursor: pointer;
  color: #00BFFF;
  transition: color 0.3s ease;
}

.banner h1:hover {
  color: #00FF94;
}